/* ========================================================================== */
/* SINGLE PRODUCT CELL */
/* ========================================================================== */

.product-card{
    padding: 0px;
    z-index: 1;
    position: relative;
    width: 100%;
    height: 100%;
    outline: 1px solid $gray-300;
    border-radius: $border-radius-main;
    overflow: hidden;
}
.product-card__link{
    padding: 0px;
    z-index: 1;
    position: relative;
    background: $white;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    color: $gray-900;
    text-align: left;
    transition: 0.3s ease box-shadow;
    height: 100%;
    padding: 8px;
}

.product-card__top{
    width: 100%;
    position: relative;

    &:hover, &:active, &:focus{
        text-decoration: none;
    }
}
.product-card__thumb{
    position: relative;
    padding-bottom: 85%;
    background-color: $gray-100;
}
.product-card__imgWrap{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.product-card__img{
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
    mix-blend-mode: multiply;
}

.product-card__tags{
    position: absolute;
    left: -10px;
    top: -10px;
    z-index: 1;
}
.product-card__tagsRight{
    position: absolute;
    left: auto;
    right: -10px;
    top: -10px;
    z-index: 1;
    text-align: right;
}
.product-card__tagSingle{
    display: block;
}
.product-card__title{
    text-align: center;
    font-size: 16px;
    font-weight: 500;
    margin: 0;
    padding: 20px;
}
.product-card__bottom{
    width:100%;
    padding: 0 20px 10px 20px;
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.product-card__description{
    font-size: 14px;
    color: $gray-500;
    text-align: center;
    height: 0;
    display: none;
}
.product-card__delivery{
    flex-grow: 1;
    line-height: 1.2;
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
}   
.product-card__delivery img{
    height: 16px;
    width: 16px;
}
.product-card__deliveryText{
    margin: 0;
    color: $green-500;
    font-weight: 500;
    text-align: left;
    font-size: 14px;
    padding-left: 6px;
    text-transform: uppercase;
}
.product-card__deliveryText.--unavailable{
    color: $gray-500;
}
.product-card__price{
    display: flex;
    flex-direction: row;
    align-items: baseline;
    justify-content: center;
    flex-wrap: wrap;
    flex-shrink: 0;
}
.product-card__priceOld{
    text-decoration: line-through;
    margin: 0 5px;
    line-height: 1;
}
.product-card__priceMain{
    color: $red-500;
    line-height: 1;
    font-size: 18px;
    font-weight: 500;    
    margin: 0 5px;
}

.product-card__btn{
    min-height: 48px;
    height: 48px;
    color: $green-500;
    background: rgba($green-500,0.1);
    font-size: 14px;
}




@media screen and (max-width:575px){
     .product-card{
        padding: 5px;
    }
    .product-card__title{
        font-size: 15px;
        line-height: 1.25;
        text-align: left;
        padding: 20px 10px 10px 10px;
    }
    .product-card__bottom{
        padding: 0 10px 20px 10px;
    }
    .product-card__delivery{
        display: none;
    }  
    .product-card__price{
        padding-top: 0;
        justify-content: flex-start;
        align-items: baseline;
    }
    .product-card__priceOld{
        margin: 0;
        margin-right: 10px;
        font-size: 14px;
    }
    .product-card__priceMain{
        margin: 0;
        font-size: 16px;
    }
    .product-card__thumb .product-card__tagsRight{
        display: none;
    }
    .product-card__thumb .product-card__tags {
        top: 5px;
        left: 5px;
    }
}
